@import url('https://fonts.googleapis.com/css2?family=Space+Grotesk:wght@300;400;500;600;700&display=swap');

:root {
  --primary-gradient: linear-gradient(90deg, #f12711 0%, #f5af19 100%);
  --background-dark: #0a0b0f;
  --card-bg: rgba(255, 255, 255, 0.03);
  --accent-color: #f5af19;
  --text-light: #ffffff;
  --text-dim: #8a8f98;
  --card-border: rgba(245, 175, 25, 0.15);
  --header-height: 70px;
  --main-height: calc(100vh - var(--header-height));
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Space Grotesk', sans-serif;
}

.App {
  background-color: var(--background-dark);
  min-height: 100vh;
  max-height: 100vh;
  overflow: hidden;
}

.header {
  height: var(--header-height);
  padding: 0 40px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: var(--card-bg);
  backdrop-filter: blur(10px);
  border-bottom: 1px solid var(--card-border);
}

.logo h1 {
  font-size: 2rem;
  font-weight: 700;
  background: var(--primary-gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  letter-spacing: -1px;
}

.main-content {
  height: var(--main-height);
  display: grid;
  grid-template-rows: auto 1fr auto;
  padding: 20px;
  gap: 20px;
}

.dealer-container {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
}

.game-container {
  display: grid;
  grid-template-columns: 1fr auto 1fr;
  gap: 30px;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.player-section {
  aspect-ratio: 3/4;
  max-height: 400px;
  padding: 20px;
  border: 1px solid var(--card-border);
  border-radius: 16px;
  background: var(--card-bg);
  backdrop-filter: blur(10px);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.card-space {
  aspect-ratio: 2.5/3.5;
  max-height: 280px;
  border: 1px dashed var(--card-border);
  margin: 15px auto;
  border-radius: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.game-controls {
  display: flex;
  flex-direction: column;
  gap: 12px;
  align-items: center;
}

.draw-button {
  background: var(--primary-gradient);
  padding: 12px 30px;
  font-size: 0.9em;
  border-radius: 8px;
  border: none;
  color: white;
  font-weight: 600;
  letter-spacing: 1px;
  cursor: pointer;
  transition: all 0.3s ease;
  text-transform: uppercase;
}

.reset-button {
  background: transparent;
  padding: 8px 20px;
  font-size: 0.8em;
  border: 1px solid var(--card-border);
  border-radius: 6px;
  color: var(--accent-color);
  cursor: pointer;
}

.bet-amount {
  background: var(--card-bg);
  padding: 8px;
  border-radius: 6px;
  border: 1px solid var(--card-border);
}

.bet-amount input {
  width: 80px;
  background: transparent;
  border: none;
  color: var(--accent-color);
  font-size: 1em;
  text-align: center;
  padding: 4px;
}

.bet-amount input:focus {
  outline: none;
}

h2 {
  font-size: 1.4em;
  font-weight: 600;
  color: var(--text-light);
  letter-spacing: -0.5px;
}

.cards-remaining {
  font-size: 0.9em;
  color: var(--text-dim);
  letter-spacing: 0.5px;
}

.scoreboard {
  background: var(--card-bg);
  border-radius: 12px;
  padding: 15px;
  border: 1px solid var(--card-border);
  max-width: 1200px;
  margin: 0 auto;
  width: 100%;
}

/* Card Styles */
.card {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 3em;
  background: white;
  border-radius: 12px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  font-weight: 600;
}

.card.red {
  color: #f12711;
}

.card.black {
  color: #1a1a2e;
}
